import { ProfileAPI, UsersAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DELETE_POST = 'DELETE_POST';
let initialState = {
        posts: [
            {id: 1, text: 'Я пишу на JSX', likes: 1},
            {id: 2, text: 'Я знаю рекат', likes: 2},
            {id: 3, text: 'Ууу сука', likes: 1},
            {id: 4, text: 'Как я тебе', likes: 2},
        ],
        profile: null,
        status: ''
}

export const profileReducer = (state = initialState,action) => {
    switch (action.type) {
        case ADD_POST:
            return {
                ...state,
                posts: [...state.posts, {id: (state.posts.length + 1), text: action.text, likes: 0}],
                newPostText: ''            
            }
            break;
            case  SET_USER_STATUS :
            return {
                ...state,
                status: action.status
            }
            break;
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.postId)
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
            break;
        default:
            return state;
            break;
    }    
}


export const addPostActionCreator = (text) => ({type: ADD_POST, text}) 
export const deletePost = (postId) => ({type: DELETE_POST, postId}) 

export const getUserProfileAC = (profile) => ({type: SET_USER_PROFILE, profile: profile}) 

export const setStatus = (status) => ({type: SET_USER_STATUS, status}) 

export const getStatus = (userId) => async (dispatch) => {      
    let data = await  ProfileAPI.getStatus(userId);
    if (data.resultCode != -1) {
        dispatch(setStatus(data.status.text));
    }

} 

export const updateStatus = (status) => async (dispatch) => {      
    let data = await ProfileAPI.updateStatus(status);
    if(data.resultCode == 0) {
        dispatch(setStatus(status));
    }
} 

export const getProfile = (userId) =>  async (dispatch) => {      
    let data = await ProfileAPI.getProfile(userId);
    dispatch(getUserProfileAC(data));
} 

export default profileReducer;