import { NavLink } from 'react-router-dom';
import css from './Sidebar.module.css';
const Navbar = (props) => {
    
    return(
        <nav className={css.nav}>
            <NavLink to="/profile" className={`${css.link}`} activeClassName={css.active}>Profile</NavLink>
            <NavLink to="/dialogs" className={`${css.link}`} activeClassName={css.active}>Messages</NavLink>
            <NavLink to="/news" className={`${css.link}`} activeClassName={css.active}>News</NavLink>
            <NavLink to="/music" className={`${css.link}`} activeClassName={css.active}>Music</NavLink>
            <NavLink to="/users" className={`${css.link}`} activeClassName={css.active}>Users</NavLink>
            <NavLink to="/settings" className={`${css.link}`} activeClassName={css.active}>Settings</NavLink>
            {/* <SideFriendsBlock state ={props.state.friends_block}/> */}
        </nav>
    )
}

const SideFriendsBlock = (props) => {
    return (
        <div>
            <div>Друзья онлайн</div>
            <div className={css.friend_block}>
                {/* {
                    props.state.map((el)=>( <SideFriends state={el} />))
                } */}
                
            </div>
        </div>
    )
}

const SideFriends = (props) => {
    return (
        <div className={css.friend_item}>
            <div className={css.friend_icon}></div>
            <div id={props.state.id} className={css.friend_label}>{props.state.name}</div>
        </div>
        
    )
}


export default Navbar