import * as axios from 'axios';

const instance = axios.create({
    baseURL: 'https://soc-api.js/api/1.0/',
    withCredentials: true,
/*     
    headers: {
        "API-KEY": "3124343434234234"
    } */
});


export const UsersAPI =  {
    getUsers(currentPage = 1,pageSize = 10) {
        return instance.get(
            'users/?page=' + currentPage + '&count=' + pageSize
            ).then(responce => {
                return responce.data;
            }            
        )        
    },
    follow(userId) {
        return instance.post(
           `/follow/${userId}`
            ).then(responce => {
                return responce.data;
            }            
        )        
    },
    unfollow(userId) {
        return instance.delete(`/follow/${userId}`,{
            withCredentials: true
        }).
        then( responce => {
                return responce.data;            
            }
        ) 
        
    }
    

}

export const ProfileAPI = {
    getProfile(userId) {
        return instance.get('profile/' + userId).
        then( 
            
            responce => {
                return responce.data;
            }
        )
    },
    getStatus(userId = '') {
        return instance.get('/auth/status/' + userId).
        then( 
            responce => {
                return responce.data;
            }
        )
    },
    updateStatus(status) {
        return instance.put('/auth/status/',{status: status}).
        then( 
            responce => {
                return responce.data;
            }
        )
    }
}


export const AuthAPI = {
    getAuthInfo() {
       return instance.get(`auth/me/`).
        then( responce => {
                return  responce.data.data;                
        }) 
    },
    login(data) {
        
        return instance.post('/auth/login/',`login=${data.login}&password=${data.password}`).
        then( 
            responce => {
                    return responce.data;              
            }
        )
    },
    logout() {
        return instance.delete('/auth/login/').
        then( 
            responce => {
                return responce.data;
            }
        )
    }
}

export const DialogsAPI = {
    sendMessage(formData) {
        console.log(formData.newMessageText)
    }
}