import './App.css';
import Navbar from './components/Sidebar/Sidebar';
import Footer from './components/Footer/Footer';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import { BrowserRouter, Route } from 'react-router-dom';
import News from './components/News/News';
/* import DialogsContainer from './components/Dialogs/DialogsContainer'; */
/* import UsersContainer from './components/Users/UsersContainer'; */
/* import ProfileContainer from './components/Profile/ProfileContainer'; */
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import React, { Suspense } from 'react';
import { connect } from "react-redux";
import { getAuthInfo } from './redux/auth-reducer';
import { InitApp } from './redux/app-reducer';
import Preloader from './components/common/Preloader/Preloader';
import store from './redux/redux_store.js';
import { Provider } from 'react-redux';
import { withSuspence } from './hoc/withSuspence';
/* organization Laading */
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

class App extends React.Component {
  
  componentDidMount() {
    this.props.InitApp();
  }
  render() {
    if (!this.props.initializedApp) {
      return <Preloader/>
    }
    
    return (
      
      
      <div className="app-wrapper">
        <HeaderContainer />
        <Navbar state={this.props.state.sidebar}/>
        <div className="app-wpapper-content">   
          <Route path="/profile/:userId?" render={withSuspence(ProfileContainer)}/>
          <Route path="/users" render={withSuspence(UsersContainer)}/>
          <Route path="/dialogs" render={withSuspence(DialogsContainer)}/>
          <Route path="/news" render={withSuspence(News)}/>
          <Route path="/music" render={()=>( <Music/> )}/>
          <Route path="/settings" render={()=>( <Settings/> )}/>      
          <Route path="/login" render={()=>( <Login/> )}/>
        </div>
        
        <Footer />
      </div>
      
    );
  }  
}

const mapStateToProps = (state) => ({
  initializedApp: state.app.initializedApp
})

let AppContainer = connect(mapStateToProps,
  {getAuthInfo, InitApp})(App)

export let SocApp = (props) => {
  return  (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
            <Provider store={store}>
            <AppContainer state={store.getState()} dispatch={store.dispatch.bind(store)} />
            </Provider>
            </BrowserRouter>
  )
}


/* export default connect(mapStateToProps,
  {getAuthInfo, InitApp})(App) */

  export default SocApp;

