import { getAuthInfo } from './auth-reducer';

const SET_INIT_APP = 'SET_INIT_APP';
let initialState = {
    initializedApp: false,


}

export const appReduce = (state = initialState,action) => {
    let stateCopy ;
    switch (action.type) {
        case SET_INIT_APP:
            return {
                ...state, 
                initializedApp: true
            }
            break;    
        default:
            return state;
            break;
    }    
}


export const InitApp = () => {    
    return  (dispatch) => {   
        let promise = dispatch(getAuthInfo())
            promise.then(()=>{
                dispatch(setInitApp());
            })
        
    }
}


export const setInitApp = () => ({type: SET_INIT_APP}) 

export default appReduce;