const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT';
const ADD_MESSAGE = 'ADD-MESSAGE';


let initialState = {
        messages:[
            {id: 1, message: 'Привет, сладенький', userType: 2, user_name: 'Никита Михалков', send_time: '09:50'},
            {id: 2, message: 'Кто ты?', userType: 1, user_name: 'Вы', send_time: '09:53'},
            {id: 3, message: 'Я, Никита Сергеевич, великий кинорежиссер', userType: 2, user_name: 'Никита Михалков', send_time: '09:53'},
            {id: 4, message: 'Зачем ты мне пишешь?', userType: 1, user_name: 'Вы', send_time: '09:57'}
        ],    
        dialog_list: [  
            {id: 2, name: 'Никита Михалков'},
            {id: 1, name: 'Олег Свиридов'},
            {id: 3, name: 'Сергей Детков'},
            {id: 4, name: 'Тамби Масаев'},
        ]
}

export const dialogsReducer = (state = initialState,action) => {

    switch (action.type) {
        case UPDATE_NEW_MESSAGE_TEXT:
            return {
                    ...state,
                    newMessageText: action.newText
                }    
            break;
    
        case ADD_MESSAGE:
            return {
                    ...state,
                    messages: [...state.messages,{id: (state.messages.length + 1), message: action.newMessageText, userType: 1, user_name: 'Вы', send_time: '09:57'}]
            }            
            break;
        default:
            return state;
    }

}

export const addMessageActionCreator = (newMessageText) => ({type: ADD_MESSAGE, newMessageText: newMessageText}) 

export default dialogsReducer;