import { AuthAPI } from "../api/api";
import { stopSubmit} from 'redux-form'
const SET_USER_DATA = 'soc/auth/SET_USER_DATA';
const SET_USER_LOGIN = 'soc/auth/SET_USER_LOGIN';
let initialState = {
            userId: null,
            email: null,
            fullName: null,
            isAuth: false

}

export const authReduce = (state = initialState,action) => {
    let stateCopy ;
    switch (action.type) {
        case SET_USER_DATA:
            let AuthState = (action.userId > 0? true : false);
            return {
                ...state, 
                ...action.data,
                userId: action.userId,
                email: action.email,
                fullName: action.fullName,
                isAuth: action.isAuth
            }
            break;    
        case SET_USER_LOGIN:
            console.log('Logined');
            break;
        default:
            return state;
            break;
    }    
}

export const getAuthInfo = () => async (dispatch) => {      
    let data = await AuthAPI.getAuthInfo();
    dispatch(setAuthUserData(data.id,data.email,data.fullName,data.isAuth));   
}

export const login = (login, password) => async (dispatch) => {      
    let data = await AuthAPI.login({login, password});
    if(data.resultCode === 0) {                
        dispatch(setAuthUserData(data.id,data.email,data.fullName, true))
        dispatch(getAuthInfo())
    }
    else {
        let message = data.messages.length > 0 ? data.messages[0] : 'some error';
        let action = stopSubmit('login',{_error: message})
        dispatch(action);
    }
} 



export const logout = () => async (dispatch) => {      
    let data = await AuthAPI.logout();
    if(data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))
    }
} 


//AC - ACtionCreator

export const loginAC = (data) => ({type: SET_USER_LOGIN, data}) 


export const setAuthUserData = (userId,email,fullName, isAuth) => ({type: SET_USER_DATA, userId: userId, email:email,fullName:fullName, isAuth }) 
export default authReduce;