import { UsersAPI } from "../api/api";
import { updateObjectInArray } from "../utils/object_helper";

const SET_USERS = 'SET_USERS';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const UNSET_USERS = 'CLEAR_USERS_FORM';
const SET_TOTAL_COUNT = 'SET-TOTAL-COUNT';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_FOLOWING_IN_PROGRESS = 'TOGGLE_FOLOWING_IN_PROGRESS';
let initialState = {
            users: [],
            totalUsersCount: 20,
            pageSize: 1,
            currentPage: 1,
            isFetching: true,
            followingInProgress: []

}



export const usersReducer = (state = initialState,action) => {
    let stateCopy ;
    switch (action.type) {
        case FOLLOW:
            return {
                ...state, 
                users: updateObjectInArray(state.users,action.userId, "id", {followed: true})
            }
            break;
    
        case UNFOLLOW: 
        return {
            ...state, 
            users: updateObjectInArray(state.users,action.userId, "id", {followed: false}) 
        }
        break;           
        case SET_USERS: 
            return {
                ...state,
                /* users: [...state.users, ... action.users] */
                users: action.users
            }          
        case SET_TOTAL_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalUsersCount
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
            break;
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
         break;
         case TOGGLE_FOLOWING_IN_PROGRESS:
            return {
                ...state,
                /* followingInProgress: action.followingInProgress */
                followingInProgress: action.followingInProgress 
                ? [...state.followingInProgress,action.userId] 
                : state.followingInProgress.filter(id => id !== action.userId)
            }
         break;
        case UNSET_USERS:
            return {
                ...state,
                users: []
            }  
        break;
        default:
            return state;
            break;
    }    
}

//AC - ACtionCreator
export const followSuccess = (userId) => ({type: FOLLOW, userId: userId}) 

export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId: userId}) 

export const setUsers = (users) => ({type: SET_USERS, users: users}) 

export const setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_COUNT, totalUsersCount: totalUsersCount}) 

export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage: currentPage}) 

export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching: isFetching})

export const toggleFollowingInProgress = (followingInProgress, userId) => ({type: TOGGLE_FOLOWING_IN_PROGRESS, followingInProgress: followingInProgress, userId: userId})



export const getUsers = (currentPage,pageSize) => {
  return async (dispatch) => {
        dispatch(toggleIsFetching( true ));
        let data = await UsersAPI.getUsers(currentPage,pageSize);
        dispatch(setCurrentPage(currentPage));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.total_count));
        dispatch(toggleIsFetching( false ));
    } 
}


export const follow = (userId) => { 
    return  async (dispatch) => {
        followUnfollowFlow(dispatch,userId, UsersAPI.follow(userId)/* .bind(UsersAPI) */,followSuccess);
    } 
}


export const unfollow = (userId) => { 
    return  async (dispatch) => {
        followUnfollowFlow(dispatch,userId, UsersAPI.unfollow(userId)/* .bind(UsersAPI) */,unfollowSuccess);
    } 
}

const followUnfollowFlow = async (dispatch,userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingInProgress(true,userId));        
    let data = await apiMethod;
    if ( data.resultCode == 0) {            
        dispatch(actionCreator(userId));
    } 
    dispatch(toggleFollowingInProgress(false,userId));
}


export default usersReducer;