import { Field, reduxForm } from 'redux-form'
import LoginCSS from './Login.module.css'
import React from 'react'
import { AuthAPI } from '../../api/api';
import { createField, Input } from '../common/FormsControls/FormControls';
import { Required } from '../../utils/validators/validators';
import { connect } from 'react-redux';
import { login } from '../../redux/auth-reducer';
import { Redirect } from 'react-router';

class Login extends React.Component {
    onSubmit = (formData) => {
        this.props.login(formData.login, formData.password)
    }
    render() {
        if (this.props.isAuth) {
            return <Redirect to={"/profile"} />
        }
        return (
            <div className={LoginCSS.formWrapper}>
                <h1>Войти</h1>
                <LoginReduxForm onSubmit={this.onSubmit}/>
            </div>
        )
    }
    
}

const LoginForm = ({handleSubmit,error}) => {
    return (
        <form className={LoginCSS.form} onSubmit={handleSubmit}>
            {createField("login", "login",[Required],Input)}
            {createField("password", "password",[Required],Input,{type: "password"})}
            {/* {createField("password", "password",[],Input,{type: "checkbox"},"remember me")} */}
            <div className={LoginCSS.rememberWrapper}>
                <Field component="input" name={"rememberme"} type="checkbox" id="rememberMe" />
                <label for="rememberMe">запомнить меня</label>
            </div>
            {
                error && <div className={LoginCSS.error_wrap}>
                    {error}
                </div>
            }
            
            <div>
                <button className={LoginCSS.loginButton}>Войти</button>     
            </div>                
        </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm)

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login} )(Login)